	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Жим в рычажном тренажере для грудных мышц позволяет имитировать жим гантелей на наклонной скамье. Однако Вы можете работать с большим отягощением, чем при жиме гантелей. Это позволит более эффективно проработать грудь.  </p>\n<p>Также стоит отметить то, что при правильной технике выполнения будет снижена нагрузка на плечевые суставы.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n    <li>Установите на рычажном тренажере необходимый вес и настройте сидение исходя из своего роста. Рукояти тренажера необходимо зафиксировать на уровне верхней части Вашей груди.</li>\n    <li>Исходное положение: голова и грудь устремите в направлении вверх, а лопатки сведите друг к другу. При помощи специальной педали поднимите вес в верхнюю позицию.</li>\n    <li>На вдохе – Плавно опускайте рукояти тренажера до тех пор, пока локти не будут согнуты до угла в 90 градусов.</li>\n    <li>На выдохе – мощным усилием сделайте жим, вернувшись к исходному положению.</li>\n    <li>Выполните необходимое количество повторений.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Старайтесь выполнять жим с максимальной амплитудой, но чтобы это не вызывало у Вас болезненных ощущений.</li>\n  <li>Вес стоит выбирать исходя из того, чтобы Вы могли выполнить не менее 6 повторений.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/f7HBLUxPaXE\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/jim-v-rychajnom-trenajere","efficiency":"7","title":"Жим в рычажном тренажере","image":"/static/0-08-0.jpg","image_2":"/static/0-08-1.jpg","image_3":"","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"3","bodypart":"Грудь","filtertype":"isolated","type":"Изолирующее","filterinventory":"blocktrenajer","inventory":"Блоки","filterplace":""}}},"pathContext":{}}

